(function() {
  'use strict';

  angular
    .module('GradeTrack')
    .controller('AssignmentsController', AssignmentsController);

  /** @ngInject */
  function AssignmentsController(GradeService) {
    var vm = this;
    vm.showDetails = false;
    vm.activeCourse = function() { return GradeService.getActiveCourse()};
    vm.addAssignment = function(assignmentName) { GradeService.addAssignment(vm.activeCourse(), "Enter Description", assignmentName)};


    vm.activeAssignment = function() { return GradeService.getActiveAssignment(); };
    vm.setActiveAssignment = function(id) { GradeService.setActiveAssignment(id); };

    vm.clickAssignment = function(id)
    {
        GradeService.setActiveAssignment(id);
        vm.showDetails = true;
    };

    vm.getGradeIndex = function(percent)
    {
      if (percent >= 0.9)
        return 0;
      else if (percent >= 0.8)
        return 1;
      else if (percent >= 0.7)
        return 2;
      else if (percent >= 0.6)
        return 3;
      return 4;
    };
    vm.changeGradeArray = function(assignment, student)
    {
      var id = assignment.id;
      var newGrade = student.assignmentGrades[id];
      var oldGrade = student.oldAssignmentGrades[id];
      var oldIndex = 4;
      if (oldGrade != null)
        oldIndex = vm.getGradeIndex(oldGrade / assignment.points);
      var newIndex = 4;
      if (newGrade != null)
        newIndex = vm.getGradeIndex(newGrade / assignment.points);
        --assignment.gradeArray[oldIndex];
        ++assignment.gradeArray[newIndex];

    };

    vm.labelArray = [ "A","B","C","D","F" ];

  }
})();

(function() {
  'use strict';

  angular
    .module('GradeTrack')
    .controller('AddClassModalController', AddClassModalController);
  /** @ngInject */
  function AddClassModalController(GradeService, $http, $scope, $uibModalInstance) {
    var vm = this;

    vm.close = function()
    {
      vm.studentString="";
      vm.courseName = "";  // reset
      vm.students = [];
      $uibModalInstance.dismiss('cancel');
    };

    vm.courseName = "";
    vm.students = [];
    vm.studentString = "";



    vm.addCourseServerPackage = function()
    {
      var c =
      {
        title: vm.courseName,
        students: [],
        instructor: ""
      };

      for (var i =0; i < vm.students.length; ++i)
      {
        c.students.push(vm.students[i].name);
      }
      c.instructor = GradeService.firstName + " " + GradeService.lastName;

      // do stuff
      $http.post("http://localhost:3000/api/info/addstudents", {
        title: c.title,
        students: c.students,
        insName: c.instructor
      });


    };

    vm.createCourse = function()
    {
      if (vm.courseName == "") return; // empty class error
      var course = GradeService.addCourse(vm.courseName);
      for (var i = 0; i < vm.students.length; ++i)
      {
        GradeService.addStudent(course, vm.students[i].name, 0);
      }
      vm.addCourseServerPackage();
      vm.close();
    };
    vm.addStudent = function(name_)
    {
      var student =
      {
        name: name_,
        id: vm.students.length
      };
      vm.students.push(student);
    };
    vm.removeStudent = function(name)
    {
      var index = vm.students.indexOf(name);
      if (index > -1)
      {
        vm.students.splice(index,1);
      }
    };
    vm.addMultipleStudents = function()
    {
      if (vm.studentString == "") return; // null check
      var names = vm.studentString;
      var result = names.split(",");
      for (var i = 0; i < result.length; ++i)
        vm.addStudent(result[i]);
      vm.studentString="";
    };


  }
})();

